// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";
@import "node_modules/keen-ui/dist/keen-ui";
*,
*::before,
*::after {
    box-sizing: border-box;
    transition: all .4s cubic-bezier(.25,.8,.25,1);
}
@font-face {
    font-family: 'NotoSans-Regular';
    src: url('../../font/NotoSansCJKjp-Regular.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NotoSans-Bold';
    src: url('../../font/NotoSansCJKjp-Bold.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NotoSans-Medium';
    src: url('../../font/NotoSansCJKjp-Medium.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'texgyreadventorbold';
    src: url('../../font/texgyreadventor-bold-webfont.woff2') format('woff2'),
         url('../../font/texgyreadventor-bold-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'texgyreadventorregular';
    src: url('../../font/texgyreadventor-regular-webfont.woff2') format('woff2'),
         url('../../font/texgyreadventor-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


@font-face {
    font-family: 'texgyreadventor';
    src: url('../../font/texgyreadventor-regular.woff2') format('woff2'), url('../../font/texgyreadventor-regular.woff') format('woff'), url('../../font/texgyreadventor-regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

.container {
    width: 100%;
    @media only screen and (min-width: 48em) {
        width: 100%;
    }
    @media only screen and (min-width: 64em) {
        width: 100%;
    }
}

html, body {
    height: 100%;
    font-size: 12px;
}

[v-cloak] {
    display: none;
}

body {
    overflow: hidden;
    margin: 0;
    padding: 0;
    width: 100%;
    display: table;
    font-weight: 400;
    line-height: 1.33em;
    letter-spacing: .01em;
    color: #1A1A1A;
    font-family: Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    position: fixed;
}

a {
    color: rgba(0,0,0,.87);
    text-decoration: none;
}

.imgResponsive {
    margin: 0 auto;
    display: block;
    max-width: 100%;
}

hr {
    border: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    height: 0;
}

/*** keen-ui overides ***/
.tooltip-element.ui-tooltip-theme {
    margin-top: 5px;
    z-index: 250;
    .tooltip-content {
        font-size: 11px;
        height: 20px;
    }
}
.ui-checkbox-checkmark {
    &:before {
        border-radius: 0;
        border: 0;
    }
}
.ui-toolbar-colored {
    background-color: #3B5998;
}
.ui-checkbox.checked {
    &:hover, &:not(.disabled):hover, &:not(.disabled), &:not(.disabled).active {
        .ui-checkbox-checkmark {
            &:before {
                background-color: #FF9600;
            }
        }
    }
    .ui-checkbox-checkmark {
        &:before {
            background-color: #FF9600;
        }
    }
}
.ui-button-normal {
    &.color-primary {
        background-color: #FF9600;
        &:hover:not([disabled]), &.dropdown-open {
            background-color: #d27b00;
        }
    }
}
.ui-button-flat {
    &.color-primary {
        color: #fff;
        &:hover:not([disabled]), &.dropdown-open {
            background-color: #FF9600;
        }
    }
}
.ui-icon-button-flat {
    &.color-primary {
        color: #3B5998;
        .ui-icon-button-icon {
            color: #3B5998;
        }
    }
}
.ui-fab.color-primary {
    background-color: #3B5998;
}
.ui-alert-body {
    &.info {
        background-color: rgba(59, 89, 152, 0.12);
        .ui-alert-icon{
            color: #3B5998;
        }
    }
}
.ui-switch.checked{
    .ui-switch-track {
        background-color: rgba(59, 89, 152, 0.5);
    }
    .ui-switch-thumb  {
        background-color: #3B5998;
    }
}
.ui-menu-option {
    height: auto;
    .ui-menu-option-content.ui-menu-default {
        font-size: 12px;
        padding: 4px 12px;
    }
}
body[modality="keyboard"] {
    .ui-toolbar {
        .ui-icon-button:focus {
            border: none;
        }
    }
}
.drop-content {
    margin-top: 6px;
    font-size: 12px;
    min-width: 250px;
    .ui-popover {
        padding: 16px 0px;
    }
    .popOver {
        max-height: 200px;
        overflow: auto;
        padding: 2px 16px 2px 16px;
    }
    .ui-alert {
        .ui-alert-body {
            min-height: 40px;
            padding: 8px 10px;
            .ui-alert-text {
                font-size: 12px;
            }
        }
    }
}

.ui-alert {
    .ui-alert-body {
        min-height: 40px;
        padding: 8px 10px;
        .ui-alert-text {
            font-size: 12px;
        }
    }
}

.ui-confirm-message {
    font-size: 13px;
}

.ui-button {
    font-size: 13px;
    border-radius: 0;
    padding: 10px 16px;
    height: auto;
    &.fullwidth {
        width: 100%;
    }
}

.ui-modal {
    font-size: 12px;
    .ui-modal-wrapper {
        padding: 30px;
        @media (max-width:1024px) {
            padding: 20px;
        }
        .ui-modal-container {
            @media(max-width: 600px) {
                width: 400px;
            }
            @media(max-width: 500px) {
                width: 100%;
            }
        }
    }
    &.ui-modal-large {
        .ui-modal-wrapper {
            .ui-modal-container {
                width: 100%;
                height: 100%;
            }
        }
    }
}
.ui-collapsible-body {
    line-height: 1.5em;
}
.ui-collapsible-header {
    font-size: 13px;
    min-height: 40px;
}
.ui-collapsible-header-content {
    @media(max-width: 500px) {
        max-width: 150px;
    }
    div {
        overflow: hidden;
        text-overflow: ellipsis;
    }
}
.ui-snackbar {
    z-index: 300;
}
.ui-snackbar-container {
    top: 20px;
    bottom: auto;
}
.ui-select {
    .ui-select-item-text {
        transition: none;
    }
    .ui-select-option {
        transition: none;
    }
}
.mainSnackBar {
    .ui-snackbar {
        .ui-snackbar-text {
            width: 100%;
            text-align: center;
        }
        .ui-snackbar-action {
            margin: 0px;
            padding-left: 0px;
        }
    }
}
.ui-fab {
    padding: 0px;
}
.ui-sidebar {
    &.staffSidebar {
        &.opened {
            .ui-sidebar-content {
                box-shadow: none;
            }
        }
    }
}
.ui-textbox-input, .ui-textbox-textarea {
    font-family: 'NotoSans-Regular';
    color: rgba(0,0,0,.87);
    background-color: #E6E6E6;
    border-bottom-color: rgba(0,0,0,0);
    padding-left: 10px;
    padding-right: 10px;
    &::placeholder {
        color: #999999;
    }
}
.ui-textbox {
    &.active:not(.disabled):not(.invalid) {
        .ui-textbox-input, .ui-textbox-textarea {
            border-bottom-color: #FF9600;
        }
    }
}
/*** keen-ui overides ***/

.section-ui-toolbar {
    .ui-button-icon {
        font-size: 30px;
    }
}

.noticesPopOver {
    &.ui-post {
        padding: 0px;
        box-shadow: none;
        .posts-container {
            .post {
                min-width: 250px;
                .details {
                    h3.title {
                        font-size: 12px;
                        margin-bottom: 5px;
                        margin-right: 60px;
                    }
                    p.content {
                        font-size: 11px;
                    }
                    .date {
                        font-size: 10px;
                    }
                }
            }
        }
    }
}
.proto {
    /*** keen-ui overides ***/
    .ui-toolbar-right {
        padding-right: 10px;
    }
    .ui-textbox-label-text {
        font-size: 12px;
    }
    .ui-textbox-content {
        .ui-textbox-label-text {
            margin-bottom: 5px;
        }
    }
    .ui-textbox-input {
        height: 28px;
        &, .ui-textbox-textarea {
            font-size: 14px;
        }
    }
    .ui-textbox-help-text {
        text-align: left;
        font-size: 12px;
    }
    .ui-textbox-counter {
        font-size: 12px;
    }
    .ui-textbox-feedback {
        min-height: 16px;
        font-size: 12px;
    }
    .hideNavIcon {
        .ui-toolbar-center {
            padding-left: 10px;
        }
    }
    .ui-button.protoButton {
        font-size: 12px;
        padding: 8px 16px;
        line-height: 1em;
        height: auto;
        &:not(:first-child) {
            margin-left: 10px;
        }
        &.signin {
            border: 1px solid rgba(255,255,255,0.3);
        }
    }
    .ui-button-group {
        .ui-button {
            margin-right: 15px;
            margin-bottom: 15px;
        }
    }
    .form-group {
        .ui-switch, .ui-button {
            margin-bottom: 12px;
        }
    }
    .ui-toolbar {
        z-index: 200;
        height: 70px;
        font-size: 15px;
        .ui-icon-button {
            width: 32px;
            height: 32px;
            .ui-icon {
                font-size: 30px;
            }
        }
        .ui-toolbar-title {
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            &.appLogo {
                img {
                    max-height: 30px;
                }
            }
            @media (max-width: 400px) {
                max-width: 100px;
            }
        }
    }
    &.pc-view {
        .ui-toolbar {
            .ui-toolbar-left {
                .ui-icon-button {
                    display: none;
                }
            }
            .ui-toolbar-title {
                &.appLogo {
                    img {
                        margin-left: 20px;
                    }
                }
            }
        }
    }
    .ui-toolbar-nav-icon {
        margin-right: 6px;
        margin-left: 6px;
    }
    .ui-icon {
        &.primary {
            color: #3B5998;
        }
    }
    .ui-modal-mask {
        position: absolute;
        .ui-modal-container {
            overflow-x: visible;
            overflow-y: visible;
            position: relative;
            width: 457px;
            padding: 42px;
            .ui-modal-header-text {
                font-size: 31px;
                color: #3B5998;
                font-family: 'NotoSans-Bold';
                i {
                    background: url(../../image/toolbar-sprite.png) no-repeat scroll 5px 0 transparent;
                    background-position-x: -217px;
                    background-position-y: -43px;
                    width: 33px;
                    height: 43px;
                    font-size: 0;
                    margin-right: 10px;
                }
            }
            .ui-modal-close-button {
                position: absolute;    
                top: 0px;
                right: -10px;
                width: 40px;
                height: 40px;
                background-color: #3B5998;
                i {
                    margin-top: 4px;
                    width: 100px;
                    color: #fff !important;
                    font-weight: bold;
                }
                // i::after{
                //     display: block;
                //     font-size: 12px;
                //     content: "";
                //     color: #fff;
                //     height: 33px;
                //     width: 56px;
                //     font-family: 'NotoSans-Medium';
                // }
            }
            .ui-modal-close-button:hover i{
                color: #fff;
            }
        }
        .ui-modal-body {
            .ui-button-normal{
                width: 100%;
                i {
                    background: url(../../image/toolbar-sprite.png) no-repeat scroll 5px 0 transparent;
                    background-position-x: -588px;
                    background-position-y: -59px;
                    font-size: 0;
                    width: 21px;
                    margin-right: 20px;
                    height: 19px;
                }
            }
            .ui-button-normal,
            .ui-button-normal:hover{
                background-color: #3B5998;
            }

            .ui-button-text {
                font-size: 21px;
                font-family: 'NotoSans-Bold';
            }
        }
    }

    .ui-modal-mask.downloadDoc .ui-modal-header-text i{
        background-position-x: -1px;
    }

    /*** keen-ui overides ***/

    .noSelect {
        -webkit-touch-callout: none; /* iOS Safari */
          -webkit-user-select: none; /* Safari */
           -khtml-user-select: none; /* Konqueror HTML */
             -moz-user-select: none; /* Firefox */
              -ms-user-select: none; /* Internet Explorer/Edge */
                  user-select: none; /* Non-prefixed version, currently
                                      supported by Chrome and Opera */
    }

    .btnDivider {
        border-right: 2px solid rgba(255,255,255,0.7);
        margin: 8px 12px;
    }

    .faqModal {
        .divTable {
            border-top: 2px solid rgba(0,0,0,0.2);
            border-bottom: 2px solid rgba(0,0,0,0.2);
            .row {
                div[class*="col-"] {
                    padding: 10px 20px;
                    &:first-child, &:last-child {
                        border: none;
                    }
                }
                .col-xs-4 {
                    background: rgba(0,0,0,0.1);
                }
            }
        }
        .title {
            display: block;
            &:before {
                content: ' ';
                border-left: 2px solid #2196f3;
                margin-right: 5px;
            }
        }
    }

    .protoSignin, .protoForgotPassword, .protoRegister {
        .protoSigninCon, .protoForgotPasswordCon, .protoRegisterCon {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            margin: 0 auto;
            display: block;
            padding: 30px;
            max-width: 320px;
            border-radius: 3px;

            @media (max-width: 500px) {
                border: 0px;
            }
            h3.protoTitle, h5.protoTitle {
                text-align: center;
                font-size: 22px;
                margin-top: 0px;
                font-weight: 400;
            }
            h5.protoTitle {
                font-size: 13px;
            }
        }
    }

    .screen-sharing {
        position: absolute;
        width: 100%;
        height: 100%;
        background: #2B2A2A;
        z-index: 700;
        video {
            // width: 90%;
            position: absolute;
            //height: 100%;
            padding-bottom: 100px;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            @media (max-width: 500px) {
                width: 100%;
            }
        }
    }

    .protoDashboard, .protoCon {
        .protoSidebar {
            &.ui-sidebar {
                .ui-sidebar-content {
                    margin-top: 70px;
                    .avatarCon {
                        padding: 40px 20px;
                        .avatar {
                            position: relative;
                            width: 175px;
                            height: 175px;
                            border-radius: 50%;
                            margin: 0 auto;
                            overflow: hidden;
                        }
                        .imgResponsive {
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .userFullName {
                        font-size: 13px;
                        text-align: center;
                        margin: 20px 0px 0px;
                    }
                    .ui-button {
                        font-size: 11px;
                        height: auto;
                        margin: 0 auto;
                        display: block;
                        .ui-icon {
                            font-size: 12px;
                        }
                    }
                    ul.sidebarMenu {
                        list-style: none;
                        padding: 0px;
                        font-size: 15px;
                        margin: 0;
                        border-bottom: 2px solid rgba(255,255,255,0.9);
                        background:#f5f5f5;
                        li a{
                            position: relative;
                            padding: 15px 40px;
                            display: block;
                            font-size: 16px;
                            color:#3B5998;
                            font-family: 'NotoSans-Medium';
                            text-transform: uppercase;
                            border-top: 2px solid rgba(255,255,255,0.9);
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            &:hover {
                                background-color: rgba(0,0,0,.06);
                            }
                            .ui-icon {
                                margin-right: 10px;
                                font-size: 30px;
                                margin-top: -3px;
                            }
                            &.v-link-active {
                                color: #fff;
                                background: #3B5998;
                                .ui-icon {
                                    color: #fff;
                                }
                            }
                        }
                    }
                }
            }
        }
        .dashboardContent, .protoConnectContainer, .protoContentContainer {
            transition-property: transform;
            will-change: transform;
            min-height: 100%;
            -ms-flex-flow: column nowrap;
            flex-flow: column;
            -ms-flex: 1;
            flex: 1;
            display: flex;
            transition: all .4s cubic-bezier(.25,.8,.25,1);

            position: absolute;
            right: 0;
            left: 0;
            top: 0;
            bottom:0;
            min-height: 100%;

            .additional-settings {
                position: absolute;
                right: 0;
                bottom: 0;
                padding-right: 20px;
                .ui-switch-label-text {
                    font-size: 12px;
                }
            }

            .invertToWhite, &.invertToWhite {
                .ui-icon, .ui-textbox-counter, .ui-textbox-help-text, .ui-textbox-label-text, .ui-alert-text {
                    color: #fff;
                }
                .ui-textbox-input {
                    color: rgba(0,0,0,.87);
                    background-color: #fff;
                    border-bottom-color: 0;
                    padding-left: 10px;
                    padding-right: 10px;
                    &::placeholder {
                        color: rgba(0,0,0,.87);
                    }
                }
                .ui-textbox {
                    &.active:not(.disabled):not(.invalid) {
                        .ui-textbox-input, .ui-textbox-textarea {
                            border-bottom-color: #FF9600;
                        }
                    }
                }
            }

            .overlay {
                background: rgba(0,0,0,0.5);
                /** 700: shareScreen */
                z-index: 701;
                position: absolute;
                display: block;
                width: 100%;
                height: 100%;
                &.blurredBg {
                    background-color: transparent;
                }
                .ui-progress-circular {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                }
            }
            .wrapper {
                position: relative;
                margin-top: 70px;
                width: 100%;
                height: 100%;
                display: block;
                overflow: auto;
                &.noToolbar {
                    margin-top: 0px;
                }
                &.noScroll {
                    overflow: hidden;
                }
                .row.middle-xs.center-xs, .peer-connect {
                    .row {
                        position: relative;
                        margin-right: 0;
                        margin-left: 0;
                    }
                }
                .alertBgWhite {
                    .ui-alert-body {
                        &.error {
                            background: rgba(255,255,255,0.7);
                        }
                    }
                }
                > .row {
                    position: absolute;
                    min-height: 100%;
                    width: 100%;
                    display: block;
                    display: flex;
                    padding: 30px;
                    margin-left: 0px;
                    @media (max-width:1024px) {
                        padding: 20px;
                    }
                }

                > hr {
                    margin-bottom: 20px;
                }
                h1 {
                    font-weight: 400;
                    line-height: 1em;
                    margin-top: 0px;
                    position: relative;
                    color: #4f92ff; //new added style
                    font-weight: 600; //new added style

                    @media (max-width: 500px) {
                        font-size: 28px;
                    }
                    .ui-fab {
                        float: right;
                        &.ui-fab-mini {
                            width: 32px;
                            height: 32px;
                            .ui-icon {
                                font-size: 20px;
                            }
                        }
                    }
                    .ui-progress-circular-toggle-transition {
                        display: inline-block;
                        padding: 0px 20px;
                    }
                }

                .ui-textbox-textarea {
                    font-size: 14px;
                    height: 30vh;
                    resize: none;
                    padding-top: 10px;
                }
                img.avatarUpload {
                    width: 120px;
                    height: 120px;
                    border-radius: 50%;
                }
                .avatarCon {
                    position: relative;
                    display: inline-block;
                    border-radius: 50%;
                    margin: 0px 0px 30px;
                    overflow: hidden;
                    input[type="file"] {
                        width: 100%;
                        height: 100%;
                        opacity: 0;
                        top: 0;
                        overflow: hidden;
                        position: absolute;
                        z-index: 15;
                        cursor: pointer;
                    }
                    .overlay {
                        opacity: 0;
                        display: block;
                        width: 100%;
                        background: rgba(0, 0, 0, 0.5);
                        z-index: 10;
                        position: absolute;
                        top: 0;
                        left: 0;
                        border-radius: 50%;
                        right: 0;
                        bottom: 0;
                        .ui-icon {
                            position: absolute;
                            top: 40%;
                            color: rgba(255,255,255,0.7);
                            left: 40%;
                            bottom: 0;
                            right: 0px;
                        }
                    }
                    &:hover .overlay {
                        opacity: 1;
                    }
                }
                .docPages {
                    .pageItem {
                        display: flex;
                        .ui-textbox {
                            width: 50%;
                            padding-right: 20px;
                        }
                        img {
                            width: 50%;
                            height: 100%;
                            box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.2);
                            margin-bottom: 2px;
                        }
                    }
                }
            }
            .blurredBg:before {
                content: ' ';
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: -1;
                background-image: url("../../image/meeting-bg-new.png");
                // background-image: url("../../image/bg1.jpg");
                // opacity: 0.7;
                

                background-repeat: no-repeat;
                background-position: 50% 0;
                -ms-background-size: cover;
                -o-background-size: cover;
                -moz-background-size: cover;
                -webkit-background-size: cover;
                background-size: cover;
            }
            .blurredBg {
                .ui-alert {
                    position: absolute;
                    top: 0;
                }
            }
            .ui-panel {
                width: 100%;
                border: none;
                box-shadow: 0 0 2px rgba(0,0,0,.12), 0 2px 2px rgba(0,0,0,.2);
                padding: 20px;
            }
            .buttonCon {
               position: relative;
               display: inline-block;
               overflow: hidden;
               .ui-button {
                    text-transform: none;
               }
            }
        }

        .dashboardContent {
            .wrapper {
